<template>
    <div id="container" v-if="NuiOpen" :style="position_nui">
        <div class="box">
            <div id="logo_store"><img src="img/logo.png" /></div>
            
            <div id="store_information">
                <p id="main_title">{{Texts_Nui.Title}}</p>
                <p id="instruction">{{Texts_Nui.Info1}}</p>
                <p id="instruction">{{Texts_Nui.Info2}}
                <span style="color: rgba(0, 255, 0, 0.75); text-shadow: 1px 2px black">{{Texts_Nui.Info3}}</span>
                </p>
                <p id="instruction">
                    {{Texts_Nui.Info4}}
                    <span style="color: rgba(255, 0, 0); text-shadow: 1px 2px black">{{Texts_Nui.Info5}}</span>
                </p>
            </div>
            
            <div id="RemoveAllTattoos" v-bind:class="{ hover2: selected == -1 }" v-if="selected==-1" :style="text_button_remove_tattoos_style">{{text_button_remove_tattoos}}</div>

            <div id="tattoos_list" v-if="selected != -1">
                <ul v-for="(item, index) in list" v-bind:key="index">
                    <li :class="item.title + item.dlc">
                        <div id="local_range" v-bind:class="{ hover: selected == index }">
                            <img src="img/machine.png" />
                            <span id="info_tatto">{{ item.title }}:<span id="mpheist3_overlays_info">{{ item.current }}</span></span>
                            <span class="dlc_valor" id="dlc_valor" style="margin-left: 0.5vh; font-size: 1.5vh; color: green">{{Texts_Nui.Money_Symbol}}{{ item.price }}</span>
                            <span class="dlc_total" id="dlc_total" style="margin-left: 0.5vh; font-size: 1.5vh; color: chocolate">{{Texts_Nui.QtyTattoos}} {{ item.qty }}</span>
                            <span class="dlc_acquired" id="dlc_acquired" style="margin-left: 0.5vh; font-size: 1.5vh; color: orange" v-if="item.acquired == 1">{{Texts_Nui.TattoosAcquired}}</span>
                            <span class="dlc_acquired" id="dlc_acquired" style="margin-left: 0.5vh; font-size: 1.5vh; color: red" v-else-if="item.acquired == 2">{{Texts_Nui.TattoosRemoved}}</span>
                            <span class="dlc_has" id="dlc_has" style="margin-left: 0.5vh; font-size: 1.5vh; color: red" v-if="item.has">{{Texts_Nui.AlreadyHaveTattoo}}</span>
                            <input type="range" min="1" :max="item.qty" value="0" class="slider" id="mpheist3_overlays" v-model="item.current"/>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Tattoo_Shop_And_Degrades",
    data() {
        return {
            NuiOpen: false,
            NameResource: 'mx_tattoos',
            position_nui: 'left: 100%; top: 100%; transform: translate(-100%, -100%) scale(1);',
            selected: 0,
            list: [],
            Texts_Nui: [],
            text_button_remove_tattoos: "",
        };
    },

    methods: {
        receiveLua: function (event) {
            if (!event || !event.data) return;

            const lua = event.data;

            if(lua.ui == "Keys") {
                this.Pressed_Key(lua.mover);
            }
            else if(lua.ui == "Interface") {
                this.NuiOpen = lua.NuiOpen;

                if (lua.NuiOpen) {
                    this.list = lua.list;
                    this.position_nui = 'left: '+lua.x+'; top: '+lua.y+'; transform: translate(-'+lua.x+', -'+lua.y+') scale('+ lua.scale +');';

                    this.Texts_Nui = lua.Texts_Nui;
                    this.text_button_remove_tattoos = this.Texts_Nui.ButtonRemoveTattoo;

                    this.NameResource = lua.NameResource;
                }  
            } 
            
            if(lua.ui == "Info") {
                for (var i = 0; i < this.list.length; i++) {
                    this.list[i].has = false;
                    if (this.list[i].dlc == lua.dlc) {
                        this.list[i].has = lua.possui;
                    }
                }
            } else if(lua.ui == "Added") {
                for (var j = 0; j < this.list.length; j++) {
                    this.list[j].acquired = 0;
                    if (this.list[j].dlc == lua.dlc) {
                        this.list[j].acquired = lua.add;
                    }
                }
            }
        },

        // Ao utilizar o teclado para selecionar no menu, o scroll segue a seleção.
        scrollToClass(className) {
            let el = this.$el.getElementsByClassName(className)[0];
            
            if(el != undefined && el != 'undefined')
                el.scrollIntoView();
        },
    
        // Teclado do menu
        Pressed_Key(key) {
            var keypress = false;
            if (key == "Up") {
                if (this.selected < 0) {
                    this.selected = this.list.length - 1;
                    setTimeout(function() { 
                        this.scrollToClass(this.list[this.selected].title + this.list[this.selected].dlc);
                    }.bind(this), 100)
                } 
                else 
                    this.selected--;
            } 
            else if (key == "Down") {
                if (this.selected >= this.list.length - 1) {
                    this.selected = -1;
                } 
                else 
                    this.selected++;
            } 
            else if (key == "Right" && this.selected >= 0) {
                keypress = true;
                if (this.list[this.selected].current >= this.list[this.selected].qty) {
                    this.list[this.selected].current = 1;
                } 
                else 
                    this.list[this.selected].current++;
            } 
            else if (key == "Left" && this.selected >= 0) {
                keypress = true;
                if (this.list[this.selected].current <= 1) {
                    this.list[this.selected].current = this.list[this.selected].qty;
                } 
                else 
                    this.list[this.selected].current--;
            } else if(key == "Enter" || key == "Delete") 
                keypress = true;

            if (keypress) {
                if (this.selected >= 0) {
                    fetch(`https://${this.NameResource}/UpdateTattoo`, {
                        method: 'POST',
                        body: JSON.stringify({
                            mx: {key: key, dlc: this.list[this.selected].dlc, current: this.list[this.selected].current},
                        }),
                    });
                } else {
                    fetch(`https://${this.NameResource}/UpdateTattoo`, {
                        method: 'POST',
                        body: JSON.stringify({
                            mx: {key: key, dlc: 'RemoverAll', current: 'RemoverAll'},
                        }),
                    });
                    this.text_button_remove_tattoos = this.Texts_Nui.ButtonTattooRemoved;
                    this.text_button_remove_tattoos_style = 'border: 0.2vh solid rgba(0, 255, 0, 0.3); color: green;';
                    setTimeout(function() {   
                        this.text_button_remove_tattoos = this.Texts_Nui.ButtonRemoveTattoo;
                        this.text_button_remove_tattoos_style = 'border: 0.2vh solid rgba(255, 0, 0, 0.3); color: red;';
                    }.bind(this), 2500)
                }
            }
            if (this.selected >= 0)
                this.scrollToClass(this.list[this.selected].title + this.list[this.selected].dlc);
        },
    },

    created: function () {
        window.addEventListener("message", this.receiveLua);
    },

    destroyed: function () {
        window.removeEventListener("message", this.receiveLua);
    },
};
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}

body {
    user-select: none;
}

#container {
    position:absolute;
	top: 0%;
	left: 0%;
	transform: translate(-0%, -0%) scale(1.0);

    color: white;
    font-family: 'Bebas Neue', cursive;
    margin-right: 1vh;
}

.box {
    width: 50vh;
    height: 94vh;
    background-color: rgb(0, 0, 0, 0.5);
    border-radius: 1.5vh;
}

.box #logo_store img {
    width: 50vh;
    height: 35vh;
    opacity: 0.75;
    border-top-left-radius: 1.5vh;
    border-top-right-radius: 1.5vh;
}

.box #main_title {
    font-size: 3vh;
    background-color: rgb(167, 167, 167, 0.15);
    text-align: center;

    border-bottom-left-radius: 4vh;
    border-bottom-right-radius: 4vh;

    padding-top: 1vh;
    padding-bottom: 1vh;

    border-bottom: 0.2vh solid rgba(167, 167, 167, 0.5);
}

.box #store_information {
    margin-top: -0.75vh;
    height: 22.25vh;
    overflow-y: scroll;
    overflow-x: hidden;
}

.box #store_information::-webkit-scrollbar {
    width: 0vh;
} /* configurando scroll */

.box #instruction {
    font-size: 2vh;
    text-align: center;
    margin-top: 1vh;
    padding-top: 1vh;
    padding-bottom: 1vh;
}

.box #tattoos_list {
    position: absolute;
    width: 50vh;
    height: 36.75vh;
    border-bottom-left-radius: 1.5vh;
    border-bottom-right-radius: 1.5vh;
    border-top-left-radius: 4vh;
    border-top-right-radius: 4vh;
    background-color: rgb(0, 0, 0, 0.15);

    overflow-y: scroll;
    overflow-x: hidden;

    border-top: 0.2vh solid rgba(167, 167, 167, 0.5);

    animation: move-open2;
    animation-duration: 0.2s;
}

.box #tattoos_list::-webkit-scrollbar {
    width: 0vh;
} /* configurando scroll */

#local_range {
    padding: 2vh 2vh;
}

#local_range li {
    position: absolute;
    font-size: 3vh;
}

#local_range img {
    position: absolute;
    widows: 6vh;
    height: 6vh;
}

#local_range:hover {
    background-color: rgb(167, 167, 167, 0.15);
    border-radius: 2vh;
    padding: 2vh 2vh;
}

.hover {
    background-color: rgb(167, 167, 167, 0.15);
    border-radius: 2vh;
    padding: 2vh 2vh;
}

#RemoveAllTattoos {
    font-size: 2vh;
    text-align: center;
    width: 30vh;

    margin-top: 6vh;
    margin-left: 10vh;

    animation: move-open2;
    animation-duration: 0.2s;

    border: 0.2vh solid rgba(255, 0, 0, 0.3);
}
.hover2 {
    background-color: rgb(167, 167, 167, 0.25);
    border-radius: 2vh;
    padding: 0.5vh 0.5vh;
    color: red;
}

#info_tatto {
    margin-left: 4.5vh;
    font-size: 2vh;
}

.dlc_total,
.dlc_valor,
.dlc_acquired,
.dlc_has {
    background-color: rgb(0, 0, 0, 0.25);
    border-radius: 5vh;
    padding: 0.5vh 0.5vh;

    animation: move-open2;
    animation-duration: 0.2s;
}

@keyframes move-open2{
    0%{
        /* top: 100%; */
        opacity: 0.0;
    }

    100%{
        /* top: 0%; */
        opacity: 1;
    }
}

.slider {
    -webkit-appearance: none;
    width: 35vh;
    height: 2vh;
    outline: none;
    background-color: rgb(28, 31, 32);
    background-size: 10vh 10vh;
    border-radius: 0.3vh;
    margin-left: 6.25vh;
}

.slider:hover {
    opacity: 1;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 1vh;
    height: 2vh;
    cursor: pointer;
    background-color: rgb(57, 139, 80, 0.75);
}

.slider::-moz-range-thumb {
    width: 30.75px;
    height: 45.75px;
    cursor: pointer;
}

#setasSimulacao {
    position: absolute;
    margin-top: 50vh;
    transform: scale(0.75);
    margin-left: -5vh;
}

#SetaEsquerda,
#SetaCima,
#SetaBaixo,
#SetaDireita {
    float: left;
    padding: 1vh 1vh;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
}

#SetaEsquerda:hover,
#SetaCima:hover,
#SetaBaixo:hover,
#SetaDireita:hover {
    background-color: rgba(0, 0, 0, 0.8);
}
</style>
